import {createAsyncThunk, createSlice, PayloadAction} from "@reduxjs/toolkit";
import axios from "axios";
import {DictType, DictTypeDetailed} from "./dicts.types";

export const getDicts = createAsyncThunk(
  'dicts/getDicts',
  async (payload, thunkAPI) => {
    try {
      const {token} = JSON.parse(localStorage.getItem('token') || '{}');
      const {data} = await axios.get(`https://imot-api.pyzzle.ru/dicts/?show_disabled=false`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      thunkAPI.dispatch(dictsSlice.actions.setDicts(data));
    } catch (error) {
      console.log(error);
    }
  }
);

export const getDict = createAsyncThunk(
  'dict/getDict',
  async (payload: string, thunkAPI) => {
    try {
      const {token} = JSON.parse(localStorage.getItem('token') || '{}');
      const {data} = await axios.get(`https://imot-api.pyzzle.ru/dict/${payload}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      thunkAPI.dispatch(dictsSlice.actions.setCurrentDict(data));
    } catch (error) {
      console.log(error);
    }
  }
);

type InitialStateType = {
  dicts: DictType[] | null,
  currentDict: DictTypeDetailed | null
};

const initialState: InitialStateType = {
  dicts: null,
  currentDict: null
};

export const dictsSlice = createSlice({
  name: 'calls',
  initialState,
  reducers: {
    setDicts(state, action: PayloadAction<DictType[]>) {
      state.dicts = action.payload;
    },
    setCurrentDict(state, action: PayloadAction<DictTypeDetailed>) {
      state.currentDict = action.payload;
    }
  }
});
